AElB,cAAc;EAChB;;;;AAKF;;AAEA;;;;AAIE;EACA;;;;AAEE;MACE,6BAA6B;MAC7B,gBAAgB;MAChB,eAAe;MACf,YAAY;MACZ,eAAe;MACf,kBAAkB;MAClB,+BAA+B;IACjC;;;;AAEE;QACE,YAAY;MACd;;;;AAEN;;+DAE+D;;;;AAC/D;EACE,aAAa;EACb,cAAc;EACd,eAAe;EACf,eAAe;EACf,OAAO;EACP,SAAS;EACT,YAAY;EACZ,aAAa;EACb,mBAAmB;AACrB;;;;AAEE;IACE,kBAAkB;EACpB;;;;AAEA;IACE,gBAAgB;IAChB,kBAAkB;IAClB,eAAe;IACf,cAAc;EAChB;;;;AAEF;EACE;IACE,gBAAgB;IAChB,WAAW;IACX,wBAAwB;EAC1B;AACF","file":"style.css","sourcesContent":["/**\r\n* Template Name: iPortfolio - v1.2.1\r\n* Template URL: https://bootstrapmade.com/iportfolio-bootstrap-portfolio-websites-template/\r\n* Author: BootstrapMade.com\r\n* License: https://bootstrapmade.com/license/\r\n*/\r\n\r\n\r\n\r\n/*--------------------------------------------------------------\r\n# General\r\n--------------------------------------------------------------*/\r\nbody {\r\n  font-family: \"Open Sans\", sans-serif;\r\n  color: #272829;\r\n}\r\n\r\na {\r\n  color: #149ddd;\r\n}\r\n\r\n  a:hover {\r\n    color: #37b3ed;\r\n    text-decoration: none;\r\n  }\r\n\r\nh1, h2, h3, h4, h5, h6 {\r\n  font-family: \"Raleway\", sans-serif;\r\n}\r\n\r\n[click],\r\n[data-ng-click],\r\n[x-ng-click] {\r\n  cursor: pointer;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Back to top button\r\n--------------------------------------------------------------*/\r\n.back-to-top {\r\n  position: fixed;\r\n  display: none;\r\n  width: 40px;\r\n  height: 40px;\r\n  border-radius: 50px;\r\n  right: 15px;\r\n  bottom: 15px;\r\n  background: #149ddd;\r\n  color: #fff;\r\n  transition: display 0.5s ease-in-out;\r\n  z-index: 99999;\r\n}\r\n\r\n  .back-to-top i {\r\n    font-size: 24px;\r\n    position: absolute;\r\n    top: 3px;\r\n    left: 9px;\r\n  }\r\n\r\n  .back-to-top:hover {\r\n    color: #fff;\r\n    background: #2eafec;\r\n    transition: background 0.2s ease-in-out;\r\n  }\r\n\r\n/*--------------------------------------------------------------\r\n# Header\r\n--------------------------------------------------------------*/\r\n#header {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  bottom: 0;\r\n  width: 300px;\r\n  transition: all ease-in-out 0.5s;\r\n  z-index: 9997;\r\n  transition: all 0.5s;\r\n  padding: 0 15px;\r\n  background: #040b14;\r\n  overflow-y: auto;\r\n}\r\n\r\n  #header .profile img {\r\n    margin: 15px auto;\r\n    display: block;\r\n    width: 120px;\r\n    border: 8px solid #2c2f3f;\r\n  }\r\n\r\n  #header .profile h1 {\r\n    font-size: 24px;\r\n    margin: 0;\r\n    padding: 0;\r\n    font-weight: 600;\r\n    -moz-text-align-last: center;\r\n    text-align-last: center;\r\n    font-family: \"Poppins\", sans-serif;\r\n  }\r\n\r\n    #header .profile h1 a, #header .profile h1 a:hover {\r\n      color: #fff;\r\n      text-decoration: none;\r\n    }\r\n\r\n  #header .profile .social-links a {\r\n    font-size: 18px;\r\n    display: inline-block;\r\n    background: #212431;\r\n    color: #fff;\r\n    line-height: 1;\r\n    padding: 8px 0;\r\n    margin-right: 4px;\r\n    border-radius: 50%;\r\n    text-align: center;\r\n    width: 36px;\r\n    height: 36px;\r\n    transition: 0.3s;\r\n  }\r\n\r\n    #header .profile .social-links a:hover {\r\n      background: #149ddd;\r\n      color: #fff;\r\n      text-decoration: none;\r\n    }\r\n\r\n#main {\r\n  margin-left: 300px;\r\n}\r\n\r\n@media (max-width: 1199px) {\r\n  #header {\r\n    left: -300px;\r\n  }\r\n\r\n  #main {\r\n    margin-left: 0;\r\n  }\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Navigation Menu\r\n--------------------------------------------------------------*/\r\n/* Desktop Navigation */\r\n.nav-menu {\r\n  padding-top: 30px;\r\n}\r\n\r\n  .nav-menu * {\r\n    margin: 0;\r\n    padding: 0;\r\n    list-style: none;\r\n  }\r\n\r\n  .nav-menu > ul > li {\r\n    position: relative;\r\n    white-space: nowrap;\r\n  }\r\n\r\n  .nav-menu a {\r\n    display: flex;\r\n    align-items: center;\r\n    color: #a8a9b4;\r\n    padding: 12px 15px;\r\n    margin-bottom: 8px;\r\n    transition: 0.3s;\r\n    font-size: 15px;\r\n  }\r\n\r\n    .nav-menu a i {\r\n      font-size: 24px;\r\n      padding-right: 8px;\r\n      color: #6f7180;\r\n    }\r\n\r\n    .nav-menu a:hover, .nav-menu .active > a, .nav-menu li:hover > a {\r\n      text-decoration: none;\r\n      color: #fff;\r\n    }\r\n\r\n      .nav-menu a:hover i, .nav-menu .active > a i, .nav-menu li:hover > a i {\r\n        color: #149ddd;\r\n      }\r\n\r\n/* Mobile Navigation */\r\n.mobile-nav-toggle {\r\n  position: fixed;\r\n  right: 15px;\r\n  top: 15px;\r\n  z-index: 9998;\r\n  border: 0;\r\n  background: none;\r\n  font-size: 24px;\r\n  transition: all 0.4s;\r\n  outline: none !important;\r\n  line-height: 1;\r\n  cursor: pointer;\r\n  text-align: right;\r\n}\r\n\r\n  .mobile-nav-toggle i {\r\n    color: #149ddd;\r\n  }\r\n\r\n.mobile-nav-active {\r\n  overflow: hidden;\r\n}\r\n\r\n  .mobile-nav-active #header {\r\n    left: 0;\r\n  }\r\n\r\n  .mobile-nav-active .mobile-nav-toggle i {\r\n    color: #fff;\r\n  }\r\n\r\n/*--------------------------------------------------------------\r\n# Hero Section\r\n--------------------------------------------------------------*/\r\n#hero {\r\n  width: 100%;\r\n  height: 100vh;\r\n  background: url(\"../img/newport.jpg\") top center;\r\n  background-size: cover;\r\n}\r\n\r\n  #hero:before {\r\n    content: \"\";\r\n    background: rgba(5, 13, 24, 0.0);\r\n    position: absolute;\r\n    bottom: 0;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    z-index: 1;\r\n  }\r\n\r\n  #hero .hero-container {\r\n    position: relative;\r\n    z-index: 2;\r\n    min-width: 500px;\r\n    margin-bottom:200px;\r\n    margin-left:200px;\r\n  }\r\n\r\n  #hero h1 {\r\n    margin: 0 0 10px 0;\r\n    font-size: 64px;\r\n    font-weight: 700;\r\n    line-height: 56px;\r\n    color: #fff;\r\n  }\r\n\r\n  #hero p {\r\n    color: #fff;\r\n    margin-bottom: 50px;\r\n    font-size: 26px;\r\n    font-family: \"Poppins\", sans-serif;\r\n  }\r\n\r\n    #hero p span {\r\n      color: #fff;\r\n      padding-bottom: 4px;\r\n      letter-spacing: 1px;\r\n      border-bottom: 3px solid #149ddd;\r\n    }\r\n\r\n@media (min-width: 1024px) {\r\n  #hero {\r\n    background-attachment: fixed;\r\n  }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  #hero h1 {\r\n    font-size: 28px;\r\n    line-height: 36px;\r\n  }\r\n\r\n  #hero h2 {\r\n    font-size: 18px;\r\n    line-height: 24px;\r\n    margin-bottom: 30px;\r\n  }\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Sections General\r\n--------------------------------------------------------------*/\r\nsection {\r\n  padding: 60px 0;\r\n  overflow: hidden;\r\n}\r\n\r\n.section-bg {\r\n  background: #f5f8fd;\r\n}\r\n\r\n.section-title {\r\n  padding-bottom: 30px;\r\n}\r\n\r\n  .section-title h2 {\r\n    font-size: 32px;\r\n    font-weight: bold;\r\n    margin-bottom: 20px;\r\n    padding-bottom: 20px;\r\n    position: relative;\r\n    color: #173b6c;\r\n  }\r\n\r\n    .section-title h2::after {\r\n      content: '';\r\n      position: absolute;\r\n      display: block;\r\n      width: 50px;\r\n      height: 3px;\r\n      background: #149ddd;\r\n      bottom: 0;\r\n      left: 0;\r\n    }\r\n\r\n  .section-title p {\r\n    margin-bottom: 0;\r\n  }\r\n\r\n/*--------------------------------------------------------------\r\n# About\r\n--------------------------------------------------------------*/\r\n.about .content h3 {\r\n  font-weight: 700;\r\n  font-size: 26px;\r\n  color: #173b6c;\r\n}\r\n\r\n.about .content ul {\r\n  list-style: none;\r\n  padding: 0;\r\n}\r\n\r\n  .about .content ul li {\r\n    padding-bottom: 10px;\r\n  }\r\n\r\n  .about .content ul i {\r\n    font-size: 20px;\r\n    padding-right: 2px;\r\n    color: #149ddd;\r\n  }\r\n\r\n.about .content p:last-child {\r\n  margin-bottom: 0;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Facts\r\n--------------------------------------------------------------*/\r\n.facts {\r\n  padding-bottom: 30px;\r\n}\r\n\r\n  .facts .count-box {\r\n    padding: 30px;\r\n    width: 100%;\r\n  }\r\n\r\n    .facts .count-box i {\r\n      display: block;\r\n      font-size: 44px;\r\n      color: #149ddd;\r\n      float: left;\r\n    }\r\n\r\n    .facts .count-box span {\r\n      font-size: 48px;\r\n      line-height: 40px;\r\n      display: block;\r\n      font-weight: 700;\r\n      color: #050d18;\r\n      margin-left: 60px;\r\n    }\r\n\r\n    .facts .count-box p {\r\n      padding: 15px 0 0 0;\r\n      margin: 0 0 0 60px;\r\n      font-family: \"Raleway\", sans-serif;\r\n      font-size: 14px;\r\n      color: #122f57;\r\n    }\r\n\r\n    .facts .count-box a {\r\n      font-weight: 600;\r\n      display: block;\r\n      margin-top: 20px;\r\n      color: #122f57;\r\n      font-size: 15px;\r\n      font-family: \"Poppins\", sans-serif;\r\n      transition: ease-in-out 0.3s;\r\n    }\r\n\r\n      .facts .count-box a:hover {\r\n        color: #1f5297;\r\n      }\r\n\r\n/*--------------------------------------------------------------\r\n# Akills\r\n--------------------------------------------------------------*/\r\n.skills .progress {\r\n  height: 50px;\r\n  display: block;\r\n  background: none;\r\n}\r\n\r\n  .skills .progress .skill {\r\n    padding: 0;\r\n    margin: 0 0 6px 0;\r\n    text-transform: uppercase;\r\n    display: block;\r\n    font-weight: 600;\r\n    font-family: \"Poppins\", sans-serif;\r\n    color: #050d18;\r\n  }\r\n\r\n    .skills .progress .skill .val {\r\n      float: right;\r\n      font-style: normal;\r\n    }\r\n\r\n.skills .progress-bar-wrap {\r\n  background: #dce8f8;\r\n}\r\n\r\n.skills .progress-bar {\r\n  width: 1px;\r\n  height: 10px;\r\n  transition: .9s;\r\n  background-color: #149ddd;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Resume\r\n--------------------------------------------------------------*/\r\n.resume .resume-title {\r\n  font-size: 26px;\r\n  font-weight: 700;\r\n  margin-top: 20px;\r\n  margin-bottom: 20px;\r\n  color: #050d18;\r\n}\r\n\r\n.resume .resume-item {\r\n  padding: 0 0 20px 20px;\r\n  margin-top: -2px;\r\n  border-left: 2px solid #1f5297;\r\n  position: relative;\r\n}\r\n\r\n  .resume .resume-item h4 {\r\n    line-height: 18px;\r\n    font-size: 18px;\r\n    font-weight: 600;\r\n    text-transform: uppercase;\r\n    font-family: \"Poppins\", sans-serif;\r\n    color: #050d18;\r\n    margin-bottom: 10px;\r\n  }\r\n\r\n  .resume .resume-item h5 {\r\n    font-size: 16px;\r\n    background: #e4edf9;\r\n    padding: 5px 15px;\r\n    display: inline-block;\r\n    font-weight: 600;\r\n    margin-bottom: 10px;\r\n  }\r\n\r\n  .resume .resume-item ul {\r\n    padding-left: 20px;\r\n  }\r\n\r\n    .resume .resume-item ul li {\r\n      padding-bottom: 10px;\r\n    }\r\n\r\n  .resume .resume-item:last-child {\r\n    padding-bottom: 0;\r\n  }\r\n\r\n  .resume .resume-item::before {\r\n    content: \"\";\r\n    position: absolute;\r\n    width: 16px;\r\n    height: 16px;\r\n    border-radius: 50px;\r\n    left: -9px;\r\n    top: 0;\r\n    background: #fff;\r\n    border: 2px solid #1f5297;\r\n  }\r\n\r\n/*--------------------------------------------------------------\r\n# Portfolio\r\n--------------------------------------------------------------*/\r\n.portfolio .portfolio-item {\r\n  margin-bottom: 30px;\r\n}\r\n\r\n.portfolio #portfolio-flters {\r\n  padding: 0;\r\n  margin: 0 auto 35px auto;\r\n  list-style: none;\r\n  text-align: center;\r\n  background: #fff;\r\n  border-radius: 50px;\r\n  padding: 2px 15px;\r\n}\r\n\r\n  .portfolio #portfolio-flters li {\r\n    cursor: pointer;\r\n    display: inline-block;\r\n    padding: 10px 15px 8px 15px;\r\n    font-size: 14px;\r\n    font-weight: 600;\r\n    line-height: 1;\r\n    text-transform: uppercase;\r\n    color: #272829;\r\n    margin-bottom: 5px;\r\n    transition: all 0.3s ease-in-out;\r\n  }\r\n\r\n    .portfolio #portfolio-flters li:hover, .portfolio #portfolio-flters li.filter-active {\r\n      color: #149ddd;\r\n    }\r\n\r\n    .portfolio #portfolio-flters li:last-child {\r\n      margin-right: 0;\r\n    }\r\n\r\n.portfolio .portfolio-wrap {\r\n  transition: 0.3s;\r\n  position: relative;\r\n  overflow: hidden;\r\n  z-index: 1;\r\n}\r\n\r\n  .portfolio .portfolio-wrap::before {\r\n    content: \"\";\r\n    background: rgba(255, 255, 255, 0.5);\r\n    position: absolute;\r\n    left: 0;\r\n    right: 0;\r\n    top: 0;\r\n    bottom: 0;\r\n    transition: all ease-in-out 0.3s;\r\n    z-index: 2;\r\n    opacity: 0;\r\n  }\r\n\r\n  .portfolio .portfolio-wrap .portfolio-links {\r\n    opacity: 1;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: -60px;\r\n    z-index: 3;\r\n    position: absolute;\r\n    transition: all ease-in-out 0.3s;\r\n    display: flex;\r\n    justify-content: center;\r\n  }\r\n\r\n    .portfolio .portfolio-wrap .portfolio-links a {\r\n      color: #fff;\r\n      font-size: 28px;\r\n      text-align: center;\r\n      background: rgba(20, 157, 221, 0.75);\r\n      transition: 0.3s;\r\n      width: 50%;\r\n    }\r\n\r\n      .portfolio .portfolio-wrap .portfolio-links a:hover {\r\n        background: rgba(20, 157, 221, 0.95);\r\n      }\r\n\r\n      .portfolio .portfolio-wrap .portfolio-links a + a {\r\n        border-left: 1px solid #37b3ed;\r\n      }\r\n\r\n  .portfolio .portfolio-wrap:hover::before {\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    opacity: 1;\r\n  }\r\n\r\n  .portfolio .portfolio-wrap:hover .portfolio-links {\r\n    opacity: 1;\r\n    bottom: 0;\r\n  }\r\n\r\n/*--------------------------------------------------------------\r\n# Services\r\n--------------------------------------------------------------*/\r\n.services .icon-box {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.services .icon {\r\n  float: left;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  width: 54px;\r\n  height: 54px;\r\n  background: #149ddd;\r\n  border-radius: 50%;\r\n  transition: 0.5s;\r\n  border: 1px solid #149ddd;\r\n}\r\n\r\n  .services .icon i {\r\n    color: #fff;\r\n    font-size: 24px;\r\n  }\r\n\r\n.services .icon-box:hover .icon {\r\n  background: #fff;\r\n}\r\n\r\n  .services .icon-box:hover .icon i {\r\n    color: #149ddd;\r\n  }\r\n\r\n.services .title {\r\n  margin-left: 80px;\r\n  font-weight: 700;\r\n  margin-bottom: 15px;\r\n  font-size: 18px;\r\n}\r\n\r\n  .services .title a {\r\n    color: #343a40;\r\n  }\r\n\r\n    .services .title a:hover {\r\n      color: #149ddd;\r\n    }\r\n\r\n.services .description {\r\n  margin-left: 80px;\r\n  line-height: 24px;\r\n  font-size: 14px;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Testimonials\r\n--------------------------------------------------------------*/\r\n.testimonials .testimonial-item {\r\n  box-sizing: content-box;\r\n  text-align: center;\r\n  min-height: 320px;\r\n}\r\n\r\n  .testimonials .testimonial-item .testimonial-img {\r\n    width: 90px;\r\n    border-radius: 50%;\r\n    margin: 0 auto;\r\n  }\r\n\r\n  .testimonials .testimonial-item h3 {\r\n    font-size: 18px;\r\n    font-weight: bold;\r\n    margin: 10px 0 5px 0;\r\n    color: #111;\r\n  }\r\n\r\n  .testimonials .testimonial-item h4 {\r\n    font-size: 14px;\r\n    color: #999;\r\n    margin: 0;\r\n  }\r\n\r\n  .testimonials .testimonial-item .quote-icon-left, .testimonials .testimonial-item .quote-icon-right {\r\n    color: #c3e8fa;\r\n    font-size: 26px;\r\n  }\r\n\r\n  .testimonials .testimonial-item .quote-icon-left {\r\n    display: inline-block;\r\n    left: -5px;\r\n    position: relative;\r\n  }\r\n\r\n  .testimonials .testimonial-item .quote-icon-right {\r\n    display: inline-block;\r\n    right: -5px;\r\n    position: relative;\r\n    top: 10px;\r\n  }\r\n\r\n  .testimonials .testimonial-item p {\r\n    font-style: italic;\r\n    margin: 0 15px 15px 15px;\r\n    padding: 20px;\r\n    background: #fff