ity: visible;\n    backface-visibility: visible;\n}\n.vbox-overlay{\n    display: -webkit-flex;\n    display: flex;\n    -webkit-flex-direction: column;\n    flex-direction: column;\n    -webkit-justify-content: center;\n    justify-content: center;\n    -webkit-align-items: center;\n    align-items: center;\n    position: fixed;\n    left: 0;\n    top: 0;\n    bottom: 0;\n    right: 0;\n    z-index: 999999;\n}\n\n/* ----- navigation ----- */\n.vbox-title{\n    width: 100%;\n    height: 40px;\n    float: left;\n    text-align: center;\n    line-height: 28px;\n    font-size: 12px;\n    padding: 6px 50px;\n    overflow: hidden;\n    position: fixed;\n    display: none;\n    left: 0;\n    z-index: 89;\n}\n.vbox-close{\n    cursor: pointer;\n    position: fixed;\n    top: -1px;\n    right: 0;\n    width: 50px;\n    height: 40px;\n    padding: 6px;\n    display: block;\n    background-position:10px center;\n    overflow: hidden;\n    font-size: 24px;\n    line-height: 1;\n    text-align: center;\n    z-index: 99;\n}\n.vbox-left{\n    cursor: pointer;\n    position: fixed;\n    left: 0;\n    height: 40px;\n    overflow: hidden;\n    line-height: 28px;\n    font-size: 12px;\n    z-index: 99;\n    display: flex;\n    align-items:center;\n}\n.vbox-num{\n    display: inline-block;\n    margin: 6px 0 6px 15px;\n}\n/* ----- Social share ----- */\n.vbox-share{\n    line-height: 28px;\n    font-size: 12px;\n    overflow: hidden;\n    position: fixed;\n    left: 0;\n    z-index: 98;\n    display: flex;\n    align-items:center;\n    justify-content: center;\n    width: 100%;\n    text-align: center;\n}\n.vbox-share svg{\n    max-height: 28px;\n    width: 28px;\n    z-index: 10;\n    margin-left: 12px;\n    margin-top: 6px;\n    margin-bottom: 6px;\n    vertical-align: middle;\n}\n\n\n/* ----- navigation ARROWS ----- */\n.vbox-next, .vbox-prev{\n    position: fixed;\n    top: 50%;\n    margin-top: -15px;\n    overflow: hidden;\n    cursor: pointer;\n    display: block;\n    width: 4